@import "base";
body {
	background-color: #FFE0DE;
	color: #00347A;
	font-size: 20px;
}

a {
	text-decoration: none;
	color: inherit;
}

#wrapper {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	min-height: 100%;
	width: 100%;
	box-sizing: border-box;
	margin: auto;
	padding: 1em;
	max-width: 1400px;
	font-size: 2.5vw;
}

.main-canvas{
	display: flex;
	flex-flow: row wrap;
	justify-content: center;
	height: 90%;
	align-items: center;
	align-content: center;
}
.main-canvas > * {
	position: relative;
	flex: 0 0 auto;
	max-height: 80%;
	width: auto;
	max-width: 45%;
}
div.logo-container{
	position: relative;
}

div.logo-container > img {
	height: auto;
	width: 100%;
}
#apply-button{
	right: 0;
	top: 0;
	position: absolute;
	display: block;
	padding: 1em;
}
footer.fest-tagline {
	text-align: center;
	width: 100%;
	margin-top: 1em;
	max-width: 100%;
}